import { MODAL_CONTAINER_ID } from '../constants';

//#region public

export function createModalContainer(
  parentContainer: HTMLElement
): HTMLElement {
  let container: HTMLElement = document.createElement('div');
  container = setStyles2ModalContainer(container);
  container.id = MODAL_CONTAINER_ID;

  parentContainer.appendChild(container);
  return container;
}

//#endregion public

//#region PRIVATE

function setStyles2ModalContainer(container: HTMLElement): HTMLElement {
  const maxWidth = 600;
  const height = 750;

  const mediaQueryMaxWidth = maxWidth + 24;
  const smMaxWidth = 'calc(100vw - 48px)';
  const mdMaxWidth = `${maxWidth}px`;

  if (window.innerWidth <= mediaQueryMaxWidth) {
    container.style.maxWidth = smMaxWidth;
  } else {
    container.style.maxWidth = mdMaxWidth;
  }

  container.style.width = '100%';
  container.style.height = `${height}px`;
  container.style.maxHeight = 'calc(100vh - 48px)';
  container.style.zIndex = '1000000'; // TODO: Check if it is the best option

  const mediaQuery: MediaQueryList = window.matchMedia(
    `(max-width: ${mediaQueryMaxWidth}px)`
  );
  mediaQuery.addEventListener('change', (event: MediaQueryListEvent): void => {
    if (event.matches) container.style.maxWidth = smMaxWidth;
    else container.style.maxWidth = mdMaxWidth;
  });

  return container;
}

//#endregion PRIVATE
