import { API_URL } from '@environment';

import { HttpAdapter, useHttpAction } from '../common';
import { Merchant } from '../models';
import { MerchantAdapter } from '../orchestrators';

export function useMerchantService(
  zenkipayKey: string,
  baseUrl: string = API_URL
): Readonly<MerchantAdapter> {
  const http: HttpAdapter = useHttpAction(zenkipayKey, baseUrl);

  return {
    async getMerchant(): Promise<Merchant> {
      const url = 'v1/merchants/plugin';
      const headers: Record<string, string> = { 'Content-Type': 'text/plain' };
      return http.post<Merchant, string>(url, zenkipayKey, { headers });
    },
  };
}
