import { setShape2Element } from '../../../functions';
import { Options } from '../../../models';
import { IFRAME_CONTAINER_ID, IFRAME_ID } from '../constants';

//#region public

export function createIFrame(
  parentContainer: HTMLElement,
  zenkipayUrl: string,
  options: Options
): HTMLIFrameElement {
  const iframeContainer: HTMLElement = createIFrameContainer(parentContainer);
  return prepareIframe(iframeContainer, zenkipayUrl, options);
}

//#endregion public

//#region PRIVATE

function createIFrameContainer(parentContainer: HTMLElement): HTMLElement {
  let container: HTMLElement = document.createElement('div');
  container = setStyles2IFrameContainer(container);
  container.id = IFRAME_CONTAINER_ID;

  parentContainer.appendChild(container);
  return container;
}

function setStyles2IFrameContainer(container: HTMLElement): HTMLElement {
  container.style.height = '100%';
  return container;
}

function prepareIframe(
  container: HTMLElement,
  zenkipayUrl: string,
  options: Options
): HTMLIFrameElement {
  let iframe: HTMLIFrameElement = document.createElement('iframe');
  if (window.origin && window.origin !== 'null') iframe.name = window.origin;
  iframe = setStyles2Iframe(iframe, options);
  iframe.src = zenkipayUrl;
  iframe.id = IFRAME_ID;

  container.appendChild(iframe);
  return iframe;
}

function setStyles2Iframe(
  iframe: HTMLIFrameElement,
  options: Options
): HTMLIFrameElement {
  iframe.style.width = '100%';
  iframe.style.height = '100%';
  iframe.style.borderWidth = '0';
  return setShape2Element(iframe, options?.style?.shape);
}

//#endregion PRIVATE
