import { API_URL } from '@environment';

import { HttpAdapter, HttpOptions, useHttpAction } from '../common';
import { Merchant, PurchaseData, Order, PreOrder } from '../models';
import { OrderAdapter } from '../orchestrators';

export function useOrderService(
  zenkipayKey: string,
  baseUrl: string = API_URL
): Readonly<OrderAdapter> {
  const http: HttpAdapter = useHttpAction(zenkipayKey, baseUrl);

  return {
    async postPreOrder(
      merchant: Merchant,
      purchaseData: PurchaseData,
      country: string
    ): Promise<Order> {
      const url = 'v1/orders/pre';
      const preOrder: PreOrder = {
        orderPlacedAt: new Date(),
        merchantId: merchant.merchantId,
        merchantName: merchant.merchantName,
        merchantUnitId: merchant.merchantUnitId,
        country: country || purchaseData.country || '',
        totalAmount: purchaseData.amount,
        currency: purchaseData.currency,
        items: purchaseData.items,
      };
      const options: HttpOptions = {
        headers: { 'Content-Type': 'application/json' },
      };
      return http.post<Order, PreOrder>(url, preOrder, options);
    },
  };
}
