import { Shape } from '../models';

export function setShape2Element<T extends HTMLElement>(
  element: T,
  shape?: Shape
): T {
  switch (shape) {
    case 'pill': {
      element.style.borderRadius = '64px';
      break;
    }
    case 'square': {
      element.style.borderRadius = '0';
      break;
    }
    default: {
      element.style.borderRadius = '8px';
    }
  }
  return element;
}
