import { API_URL } from '@environment';
import { json2String } from '@shared/common/functions/objects';

import { HttpOptions, RequestMethod, RequestOptions } from '../models';
import { HttpAdapter } from '../orchestrators';

export function useHttpService(
  baseUrl: string = API_URL
): Readonly<HttpAdapter> {
  async function request<Result, Body>(
    url: string,
    method: RequestMethod,
    options?: RequestOptions<Body>
  ): Promise<Result> {
    url = `${baseUrl}${url}`;
    let body: string | null = null;
    let headers: Record<string, string> | null = null;
    if (options) {
      if (options?.body) body = json2String(options.body);
      if (options?.headers) headers = options.headers;
    }
    const request: RequestInit = {
      method,
      ...(body && { body }),
      ...(headers && { headers }),
    };
    // TODO: Change `fetch` per `XmlHttpRequest`
    const response: Response = await fetch(url, request);
    return response.json();
  }

  return {
    async get<Result = unknown>(
      url: string,
      options?: HttpOptions
    ): Promise<Result> {
      return request(url, 'GET', options);
    },

    async post<Result = unknown, Body = unknown>(
      url: string,
      body: Body,
      options?: HttpOptions
    ): Promise<Result> {
      const requestOpts: RequestOptions<Body> = { ...options, body };
      return request(url, 'POST', requestOpts);
    },

    async put<Result = unknown, Body = unknown>(
      url: string,
      body: Body,
      options?: HttpOptions
    ): Promise<Result> {
      const requestOpts: RequestOptions<Body> = { ...options, body };
      return request(url, 'PUT', requestOpts);
    },

    async patch<Result = unknown, Body = unknown>(
      url: string,
      body: Body,
      options?: HttpOptions
    ): Promise<Result> {
      const requestOpts: RequestOptions<Body> = { ...options, body };
      return request(url, 'PATCH', requestOpts);
    },

    async delete<Result = unknown>(
      url: string,
      options?: HttpOptions
    ): Promise<Result> {
      return request(url, 'DELETE', options);
    },
  };
}
