import { API_URL, SUFFIX_ENV_SCRIPT, ZENKIPAY_URL } from '@environment';
import { POST_MSG_TYPE } from '@shared/common/constants';

import { getZenkipayKey } from './functions';
import { FnCallback, Options, ZenkiPay } from './models';
import { createButton, createModal, getZenkipayUrl } from './orchestrators';

// eslint-disable-next-line no-var
export var { button, openModal }: ZenkiPay = (function (
  baseApiUrl: string,
  baseZenkipayUrl: string,
  suffixEnvScript: string
): ZenkiPay {
  return { button, openModal };

  async function button(
    containerId: string,
    options: Options,
    callback?: FnCallback
  ): Promise<void> {
    if (!options?.zenkipayKey) {
      options.zenkipayKey = getZenkipayKey(suffixEnvScript);
    }
    if (!options?.zenkipayKey) {
      const error: Error = new Error('Zenkipay key is undefined');
      if (callback) {
        return callback(error, null, {
          postMsgType: POST_MSG_TYPE.ERROR,
          isCompleted: true,
        });
      }
      throw error;
    }

    await createButton(
      containerId,
      options,
      (): Promise<void> => openModal(options, callback),
      callback
    );
  }

  async function openModal(
    options: Options,
    callback?: FnCallback
  ): Promise<void> {
    let zenkipayKey: string | undefined = options?.zenkipayKey;
    if (!zenkipayKey) {
      zenkipayKey = getZenkipayKey(suffixEnvScript);
    }
    if (!zenkipayKey) {
      const error: Error = new Error('Zenkipay key is undefined');
      if (callback) {
        return callback(error, null, {
          postMsgType: POST_MSG_TYPE.ERROR,
          isCompleted: true,
        });
      }
      throw error;
    }

    try {
      const zenkipayUrl: string = await getZenkipayUrl(
        zenkipayKey,
        baseApiUrl,
        baseZenkipayUrl,
        options.purchaseData
      );
      await createModal(baseZenkipayUrl, zenkipayUrl, options, callback);
    } catch (error) {
      if (callback) {
        return callback(error as Error, null, {
          postMsgType: POST_MSG_TYPE.ERROR,
          isCompleted: true,
        });
      }
      throw error as Error;
    }
  }
})(API_URL, ZENKIPAY_URL, SUFFIX_ENV_SCRIPT);

// eslint-disable-next-line @typescript-eslint/no-explicit-any
(window as any).zenkiPay = { button, openModal };
