import { ROOT_CONTAINER_ID } from '../constants';

//#region public

export function createRootContainer(): HTMLElement {
  let container: HTMLElement | null =
    document.getElementById(ROOT_CONTAINER_ID);
  container && document.body.removeChild(container);

  container = document.createElement('div');
  container = setStyles2RootContainer(container);
  container.id = ROOT_CONTAINER_ID;

  document.body.appendChild(container);
  return container;
}

//#endregion public

//#region PRIVATE

function setStyles2RootContainer(container: HTMLElement): HTMLElement {
  container.style.top = '0';
  container.style.left = '0';
  container.style.width = '100%';
  container.style.height = '100vh';
  container.style.position = 'fixed';
  container.style.alignItems = 'center';
  container.style.flexDirection = 'column';
  container.style.justifyContent = 'center';
  container.style.transition = 'opacity 250ms ease-in-out';
  container.style.backgroundColor = 'rgba(0, 0, 0, 0.5)';
  container.style.pointerEvents = 'auto';
  container.style.visibility = 'visible';
  container.style.zIndex = '999999'; // TODO: Check if it is the best option
  container.style.display = 'flex';
  container.style.opacity = '1';
  return container;
}

//#endregion PRIVATE
