import { Merchant, Order, PurchaseData } from '../models';
import { useCountryService, useOrderService } from '../services';
import { useMerchantService } from '../services/merchant.service';
import { CountryAdapter, MerchantAdapter, OrderAdapter } from './ports';

export async function getZenkipayUrl(
  zenkipayKey: string,
  baseApiUrl: string,
  baseZenkipayUrl: string,
  purchaseData: PurchaseData
): Promise<string> {
  const countryService: CountryAdapter = useCountryService();
  const country: string = await countryService.getCountry();

  const merchantService: MerchantAdapter = useMerchantService(
    zenkipayKey,
    baseApiUrl
  );
  const merchant: Merchant = await merchantService.getMerchant();

  const orderService: OrderAdapter = useOrderService(zenkipayKey, baseApiUrl);
  const { orderId }: Order = await orderService.postPreOrder(
    merchant,
    purchaseData,
    country
  );

  return `${baseZenkipayUrl}?zenkipayKey=${zenkipayKey}&orderId=${orderId}`;
}
