import { PostMessage } from '@shared/common/classes';
import { POST_MSG_TYPE } from '@shared/common/constants';
import { DoneMsg } from '@shared/common/models';

import { FnCallback, Options } from '../../models';
import {
  createRootContainer,
  createModalContainer,
  createCloseButton,
  createIFrame,
} from './orchestrators';

export async function createModal(
  baseZenkipayUrl: string,
  zenkipayUrl: string,
  options: Options,
  callback?: FnCallback
): Promise<HTMLElement> {
  return new Promise<HTMLElement>(
    (resolve: (output: HTMLElement) => void): void => {
      const rootContainer: HTMLElement = createRootContainer();
      const modalContainer: HTMLElement = createModalContainer(rootContainer);
      const closeButton: HTMLButtonElement = createCloseButton(
        modalContainer,
        options
      );
      createIFrame(modalContainer, zenkipayUrl, options);

      window.addEventListener('message', handleZenkipayMessages);
      closeButton.addEventListener('click', cancelModal);

      function handleZenkipayMessages({
        data: content,
        origin,
      }: MessageEvent<PostMessage>): void {
        if (!baseZenkipayUrl?.includes(origin)) return;

        switch (content.type) {
          case POST_MSG_TYPE.DONE: {
            if (callback) {
              callback(null, content.data as DoneMsg, {
                postMsgType: POST_MSG_TYPE.DONE,
                isCompleted: false,
              });
            }
            hideCloseButton();
            break;
          }
          case POST_MSG_TYPE.CANCEL: {
            cancelModal();
            break;
          }
          case POST_MSG_TYPE.CLOSE: {
            if (callback) {
              callback(null, content.data as DoneMsg, {
                postMsgType: POST_MSG_TYPE.CLOSE,
                isCompleted: true,
              });
            }
            removeModal();
            break;
          }
          case POST_MSG_TYPE.ERROR: {
            if (callback) {
              callback(content.data as Error, null, {
                postMsgType: POST_MSG_TYPE.ERROR,
                isCompleted: true,
              });
            }
            removeModal();
            break;
          }
          case POST_MSG_TYPE.HIDE_CLOSE_BTN: {
            hideCloseButton();
            break;
          }
        }
      }

      function hideCloseButton(): void {
        closeButton.removeEventListener('click', cancelModal);
        const container: HTMLElement | null = closeButton.parentElement;
        if (container) {
          const parentContainer: HTMLElement | null = container.parentElement;
          container.removeChild(closeButton);
          parentContainer && parentContainer.removeChild(container);
        } else {
          closeButton.style.display = 'none';
        }
      }

      function cancelModal(): void {
        if (callback) {
          callback(null, null, {
            postMsgType: POST_MSG_TYPE.CANCEL,
            isCompleted: true,
          });
        }
        removeModal();
      }

      function removeModal(): void {
        closeButton.removeEventListener('click', cancelModal);
        window.removeEventListener('message', handleZenkipayMessages);
        document.body.removeChild(rootContainer);
        resolve(rootContainer);
      }
    }
  );
}
