import { getQueryParams } from './query-params';

export function getZenkipayKey(suffixEnvScript: string): string | undefined {
  const zenkipayKey: string | undefined = getZenkipayKeyFromCurrentScript();
  if (zenkipayKey) return zenkipayKey;
  return getZenkipayKeyUsingSelector(suffixEnvScript);
}

function getZenkipayKeyFromCurrentScript(): string | undefined {
  const script: HTMLScriptElement =
    document?.currentScript as HTMLScriptElement;
  if (script) return getZenkipayKeyFromScript(script);
  return undefined;
}

function getZenkipayKeyUsingSelector(
  suffixEnvScript: string
): string | undefined {
  const scriptName = suffixEnvScript
    ? `zenkipay.${suffixEnvScript}.js`
    : 'zenkipay.js';
  const scriptSelector = `script[src*="${scriptName}?zenkipayKey="]`;
  const script: HTMLScriptElement | null =
    document.querySelector(scriptSelector);
  if (script) return getZenkipayKeyFromScript(script);
  return undefined;
}

function getZenkipayKeyFromScript(
  script: HTMLScriptElement
): string | undefined {
  const queryParams: string = script?.src?.replace(/^[^?]+\??/, '');
  const { zenkipayKey }: Record<string, string> = getQueryParams(queryParams);
  return zenkipayKey;
}
