import { COUNTRY_API } from '@environment';
import { Localization } from '@shared/common/models';

import { HttpAdapter, useHttpService } from '../common';
import { CountryAdapter } from '../orchestrators';

export function useCountryService(
  baseUrl: string = COUNTRY_API
): Readonly<CountryAdapter> {
  const http: HttpAdapter = useHttpService(baseUrl);

  return {
    async getCountry(): Promise<string> {
      const url = 'json';
      const { country_code }: Localization = await http.get<Localization>(url);
      return country_code;
    },
  };
}
