import { ZENKIPAY_ASSETS_URL } from '@environment';

import { setShape2Element } from '../../../functions';
import { Options } from '../../../models';
import { CLOSE_BTN_CONTAINER_ID, CLOSE_BTN_ID } from '../constants';

//#region public

export function createCloseButton(
  parentContainer: HTMLElement,
  options: Options
): HTMLButtonElement {
  const closeButtonContainer: HTMLElement =
    createCloseButtonContainer(parentContainer);
  return prepareCloseButton(closeButtonContainer, options);
}

//#endregion public

//#region PRIVATE

function createCloseButtonContainer(parentContainer: HTMLElement): HTMLElement {
  let container: HTMLElement = document.createElement('div');
  container = setStyles2CloseButtonContainer(container);
  container.id = CLOSE_BTN_CONTAINER_ID;

  parentContainer.appendChild(container);
  return container;
}

function setStyles2CloseButtonContainer(container: HTMLElement): HTMLElement {
  container.style.position = 'relative';
  return container;
}

function prepareCloseButton(
  parentContainer: HTMLElement,
  options: Options
): HTMLButtonElement {
  let button: HTMLButtonElement = document.createElement('button');
  button = setStyles2CloseButton(button, options);
  button.id = CLOSE_BTN_ID;
  createCloseIcon(button);

  parentContainer.appendChild(button);
  return button;
}

function createCloseIcon(parentContainer: HTMLElement): HTMLImageElement {
  const image: HTMLImageElement = document.createElement('img');
  image.src = `${ZENKIPAY_ASSETS_URL}icons/icon-close-light.svg`;
  parentContainer.appendChild(image);
  return image;
}

function setStyles2CloseButton(
  button: HTMLButtonElement,
  options: Options
): HTMLButtonElement {
  button.style.padding = '8px';
  button.style.position = 'absolute';
  button.style.backgroundColor = '#efefef';
  button.style.boxShadow = '-1px 1px 4px 1px rgba(0, 0, 0, 0.25)';
  button.style.appearance = 'none';
  button.style.cursor = 'pointer';
  button.style.borderWidth = '0';
  button.style.height = '40px';
  button.style.width = '40px';
  button.style.right = '-16px';
  button.style.top = '-16px';
  return setShape2Element(button, options?.style?.shape);
}

//#endregion PRIVATE
