import { API_URL } from '@environment';
import { Authorization } from '@shared/common/models';

import { HttpOptions } from '../models';
import { useHttpService } from '../services';
import { HttpAdapter } from './ports';

export function useHttpAction(
  zenkipayKey: string,
  baseUrl: string = API_URL
): Readonly<HttpAdapter> {
  const httpService: HttpAdapter = useHttpService(baseUrl);

  async function appendAuthorizationInHeaders(
    headers: Record<string, string> = {}
  ): Promise<Record<string, string>> {
    const url = 'public/v1/merchants/plugin/token';
    const options: HttpOptions = { headers: { 'Content-Type': 'text/plain' } };
    const { token_type, access_token }: Authorization = await httpService.post<
      Authorization,
      string
    >(url, zenkipayKey, options);
    headers['Authorization'] = `${token_type} ${access_token}`;
    return headers;
  }

  return {
    async get<Result = unknown>(
      url: string,
      options: HttpOptions = {}
    ): Promise<Result> {
      options.headers = await appendAuthorizationInHeaders(options?.headers);
      return httpService.get(url, options);
    },

    async post<Result = unknown, Body = unknown>(
      url: string,
      body: Body,
      options: HttpOptions = {}
    ): Promise<Result> {
      options.headers = await appendAuthorizationInHeaders(options?.headers);
      return httpService.post(url, body, options);
    },

    async put<Result = unknown, Body = unknown>(
      url: string,
      body: Body,
      options: HttpOptions = {}
    ): Promise<Result> {
      options.headers = await appendAuthorizationInHeaders(options?.headers);
      return httpService.put(url, body, options);
    },

    async patch<Result = unknown, Body = unknown>(
      url: string,
      body: Body,
      options: HttpOptions = {}
    ): Promise<Result> {
      options.headers = await appendAuthorizationInHeaders(options?.headers);
      return httpService.patch(url, body, options);
    },

    async delete<Result = unknown>(
      url: string,
      options: HttpOptions = {}
    ): Promise<Result> {
      options.headers = await appendAuthorizationInHeaders(options?.headers);
      return httpService.delete(url, options);
    },
  };
}
