import { ZENKIPAY_ASSETS_URL } from '@environment';

import { setShape2Element } from '../functions';
import { FnCallback, FnClickButton, Options } from '../models';

//#region PUBLIC

export async function createButton(
  containerId: string,
  options: Options,
  click: FnClickButton,
  callback?: FnCallback
): Promise<HTMLButtonElement> {
  const container: HTMLElement = getContainer(containerId);

  let button: HTMLButtonElement = document.createElement('button');
  button.id = 'pay-with-zenkipay';
  container.appendChild(button);

  const zenkipayImg: HTMLImageElement = createZenkipayImg(button, options);
  setStyles2ZenkipayImg(zenkipayImg, options);

  button = setStyles2Button(button, options);
  return setEvents2Button(button, options, click, callback);
}

//#endregion PUBLIC

//#region PRIVATE

function getContainer(containerId: string): HTMLElement {
  if (!containerId) {
    throw new Error('"containerId" is undefined');
  }

  const container: HTMLElement | null = document.getElementById(containerId);
  if (!container) {
    throw new Error(`Container with id '${containerId}' not found`);
  }

  return container;
}

async function setEvents2Button(
  button: HTMLButtonElement,
  options: Options,
  click: FnClickButton,
  callback?: FnCallback
): Promise<HTMLButtonElement> {
  return new Promise<HTMLButtonElement>(
    (resolve: (output: HTMLButtonElement) => void): void => {
      button.addEventListener('click', async (): Promise<void> => {
        switch (options?.style?.theme) {
          case 'dark': {
            button.style.backgroundColor = '#060606';
            button.style.borderColor = '#e2e2e2';
            break;
          }
          default: {
            button.style.backgroundColor = '#f8f8f8';
            button.style.borderColor = '#e2e2e2';
          }
        }
        button.style.cursor = 'not-allowed';
        button.disabled = true;

        await click(callback);
        setStyles2Button(button, options);
        button.disabled = false;
        resolve(button);
      });
    }
  );
}

function setStyles2Button(
  button: HTMLButtonElement,
  options: Options
): HTMLButtonElement {
  switch (options?.style?.theme) {
    case 'dark': {
      button.style.backgroundColor = '#000';
      button.style.borderColor = '#e2e2e2';
      button.style.borderStyle = 'solid';
      button.style.borderWidth = '1px';
      break;
    }
    default: {
      button.style.backgroundColor = '#fff';
      button.style.borderColor = '#e2e2e2';
      button.style.borderStyle = 'solid';
      button.style.borderWidth = '1px';
    }
  }

  switch (options?.style?.size) {
    case 'sm': {
      button.style.width = '130px';
      button.style.height = '25px';
      break;
    }
    case 'lg': {
      button.style.width = '300px';
      button.style.height = '50px';
      break;
    }
    default: {
      button.style.width = '196px';
      button.style.height = '38px';
    }
  }

  switch (options?.style?.expand) {
    case 'block': {
      button.style.width = '100%';
      break;
    }
    default:
  }

  button.style.cursor = 'pointer';
  return setShape2Element(button, options?.style?.shape);
}

function createZenkipayImg(
  parentContainer: HTMLElement,
  options: Options
): HTMLImageElement {
  const img: HTMLImageElement = document.createElement('img');

  switch (options?.style?.theme) {
    case 'dark': {
      img.src = `${ZENKIPAY_ASSETS_URL}images/zenkipay-dark.svg`;
      break;
    }
    default: {
      img.src = `${ZENKIPAY_ASSETS_URL}images/zenkipay-light.svg`;
    }
  }

  parentContainer.appendChild(img);
  return img;
}

function setStyles2ZenkipayImg(
  img: HTMLImageElement,
  options: Options
): HTMLImageElement {
  switch (options?.style?.size) {
    case 'sm': {
      img.style.height = '22px';
      break;
    }
    case 'lg': {
      img.style.height = '46px';
      break;
    }
    default: {
      img.style.height = '34px';
    }
  }

  return img;
}

//#endregion PRIVATE
