export function getQueryParams(queryParams: string): Record<string, string> {
  const params: Record<string, string> = {};
  if (!queryParams) return params;

  const pairs: string[] = queryParams.split(/[;&]/);
  for (let i = 0; i < pairs.length; i++) {
    const keyVal: string[] = pairs[i].split('=');
    if (!keyVal || keyVal.length != 2) continue;

    const key: string = decodeURI(keyVal[0]);
    let val: string = decodeURI(keyVal[1]);
    val = val.replace(/\+/g, ' ');
    params[key] = val;
  }
  return params;
}
